import styled from '@emotion/styled';
import { ModalTypes } from '../Types';

export const Header: ModalTypes['Parts']['Header'] = (props) => {
  const { children, ...rest } = props;
  return (
    <HeaderWrapper {...rest}>
      {children}
    </HeaderWrapper>
  );
};

const HeaderWrapper = styled.div<ModalTypes['Props']['Header']>`
  padding: ${({ p = '16px' }) => p};
  background-color: ${({ b = '#f8f9fa' }) => b};
  border-bottom: 1px solid #dee2e6;
  font-size: 1.25rem;
  font-weight: bold;
`;